.detail {

  &-container {
    min-width: 1000px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 60px;

    &:after {
      content: " ";
      visibility: hidden;
      display: block;
      height: 0;
      clear: both;
    }

  }

  &-hr {
    border: 0;
    border-top: 1px solid #eaeaea;
    margin: 40px 0;
  }

  &-image {
    float: left;
    width: 50%;
    text-align: center;
    padding: 20px;
  }

  &-title {
    margin: 0;
    font: 700 33px Roboto, sans-serif;
  }

  &-info {
    float: left;
    width: 50%;
    background: #fff;
    border-radius: 1px;
    padding: 40px;
    box-shadow: 0 3px 3px rgba(0,0,0,.1),0 1px 1px rgba(0,0,0,.1);
  }

  &-price {

    &-block {
      display: inline-block;
    }

    &_thr {
      text-decoration: line-through;
    }

    &__old {
      font: 300 20px Roboto, sans-serif;
    }
    &__old-sig {
      font: 300 20px Roboto, sans-serif;
    }

    &__new {
      font: 700 33px Roboto, sans-serif;
      color: #e06125;
    }
    &__new-sig {
      font: 700 33px Roboto, sans-serif;
      color: #e06125;
    }

  }

  &-profit {
    font: 300 20px Roboto, sans-serif;

    &-block {
      display: inline-block;
      vertical-align: top;
      font: 300 15px Roboto, sans-serif;
      color: #fff;
      padding: 5px 7px;
      background: #e06125;
      border-radius: 3px;
    }

    &-sig {
      font: 300 20px Roboto, sans-serif;
    }

    &-sig {
      font: 300 20px Roboto, sans-serif;
    }

    &-text {
      font: 300 20px Roboto, sans-serif;
    }

  }

  &-seller {

    &-block {
      display: block;
      position: relative;
      padding: 30px 0 0 0;
    }

    &__title {
      display: inline-block;
      font: 700 14px Roboto, sans-serif;
      color: #000;
      margin: 0 5px 0 0;
    }

    &__reviews {
      font: 500 14px Roboto, sans-serif;
      color: #b1b3bc;
    }

    &__addr {
      font: 500 14px Roboto, sans-serif;
      color: #b1b3bc;
    }

    &__price {
      font: 500 14px Roboto, sans-serif;
      color: #e06125;
      padding-top: 10px;
    }

    &__distance {
      position: absolute;
      top: 45px;
      right: 0;
      font: 500 14px Roboto, sans-serif;
      color: #b1b3bc;

      & i {
        font-size: 20px;
        color: #3fb166;
      }

    }

  }

  &-btn-group {
    display: block;
  }

  &-add {
    display: inline-block;
    margin-top: 20px;
    font: 700 14px Roboto, sans-serif;
    color: #000;
    text-decoration: none;
    transition: all .2s;

    & i {
      position: relative;
      top: 4px;
      width: 50px;
      height: 50px;
      padding: 16px;
      margin-right: 10px;
      color: #fff;
      background: #4a90e2;
      font-size: 20px;
      border-radius: 50%;
      cursor: pointer;
      transition: all .2s;

      &:hover {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
      }

      &:active {
        background: #4383ce;
      }

    }

  }

  &-like {
    position: relative;
    display: inline-block;
    margin-top: 20px;
    float: right;
    font: 700 14px Roboto, sans-serif;
    color: #000;
    text-decoration: none;
    transition: all .2s;

    & i {
      position: relative;
      top: 4px;
      width: 50px;
      height: 50px;
      padding: 16px 17px;
      margin-right: 10px;
      color: #fff;
      background: #51c91c;
      font-size: 20px;
      border-radius: 50%;
      cursor: pointer;
      transition: all .2s;

      &:hover {
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
      }

      &:active {
        background: #339053;
      }

    }
  }

  &-table {
    display: table;
    font: 500 14px Roboto, sans-serif;

    &__row {
      display: table-row;
    }

    &__cell {
      display: table-cell;
      padding-bottom: 20px;

      &:first-child {
        white-space: nowrap;
        padding-right: 20px;
      }

    }

  }

  &-tab {
    display: block;
    margin: 20px 0;
    float: left;
    width: 100%;
    background: #fff;
    border-radius: 1px;
    box-shadow: 0 3px 3px rgba(0,0,0,.1),0 1px 1px rgba(0,0,0,.1);

    &__title-wrapper {
      text-align: center;
      border-bottom: 1px solid #eaeaea;
    }

    &__title {
      display: inline-block;
      padding: 20px 10px;
      font: 500 14px Roboto, sans-serif;
      color: #3fb166;
      text-decoration: none;
      transition: color .2s;

      &:hover {
        color: #0D47A1;
      }

      &_active {
        color: #000;

        &:hover {
          cursor: default;
          color: #000;
        }

      }

    }

    &__title-count {
      display: inline-block;
      padding-left: 5px;
      color: #b1b3bc;
    }

    &__content{
      position: relative;
      padding: 20px;

      &:after {
        content: " ";
        visibility: hidden;
        display: block;
        height: 0;
        clear: both;
      }

    }

    &__seller {

      &-container {
        width: 50%;
        max-height: 500px;
        float: left;
        overflow: auto;
      }

      &-block {
        display: table;
        border-collapse: collapse;
        width:100%;
      }

      &-item {
        display: table-row;
        border-bottom: solid white 30px;
      }

      &-count {
        display: table-cell;
        vertical-align: middle;
        font: 500 14px Roboto, sans-serif;
        color: #b1b3bc;
        padding-right: 10px;
      }

      &-img {
        display: table-cell;
        vertical-align: middle;
        width: 100px;
        padding-right: 10px;

        & .image {
          width: 100px;
          height: auto;
        }

      }

      &-info {
        display: table-cell;
        vertical-align: top;
        padding-right: 10px;
        margin-bottom: 20px;
      }

      &-distance {
        display: table-cell;
        position: relative;
        vertical-align: middle;
        white-space: nowrap;
        font: 500 14px Roboto, sans-serif;
        color: #757575;

        & .icon {
          font-size: 20px;
          color: #3fb166;
        }

      }

    }

    &__seller-map {
      width: 50%;
      height: 500px;
      float: left;
    }

  }

}
