.toolbar  {
  display: block;
  padding: 40px 0;
  width: 100%;

  &__title {
    position: relative;
    top: 3px;
    display: inline-block;
    margin: 0 10px 0 60px;
    font: 500 20px Roboto,sans-serif;
    text-transform: uppercase;
    color: #000;
  }

  &__btn {
    display: inline-block;
    margin: 0;
    font: 700 14px Roboto,sans-serif;
    color: #000;
    text-decoration: none;
    padding: 10px;
    border-radius: 5px;
    transition: background .2s;
    cursor: pointer;

    &:hover {
      background-color: #fff;
    }

    &_active {
      cursor: default;
      border: solid 1px #b8d6ed;
      background: #fff;
    }

  }

}
