.header-container {
  background-color: #aadd12;
  height: 80px;
}

.logo {
  display: inline-block;
  margin: 7px 0 7px 35px;
  width: 200px;
  height: 65px;
  background: url(../images/logo.png);
}

.search-wrapper {
  display: inline-block;
  position: relative;
  width: calc(100% - 560px);
  //max-width: 1300px;
  vertical-align: top;
  margin: 20px 20px;
}

.search-field {
  display: block;
  width: 100%;
  padding: 10px 10px 10px 50px;
  border-radius: 5px;
  border-style: none;
  outline: 0;
  font: 400 16px Roboto,sans-serif;
  background-color: #d5ee89;
  box-shadow: 0 1px 0 0 #83ab0e;
  color: #000;
  transition: background .2s;

  &::-webkit-input-placeholder { color: #689F38; }

  &:-moz-placeholder { color: #689F38; }

  &::-moz-placeholder { color: #689F38; }

  &:-ms-input-placeholder { color: #689F38; }

  &:focus {
    color: #000;
    background-color: #fff;
  }

}

.search-icon {
  position: absolute;
  top: 12px;
  left: 20px;
  color: #689F38;
}

.user-wrapper {
  display: inline-block;
  margin: 0 60px 0 0;
  width: 200px;
  vertical-align: top;
  float: right;

  & .user-login-btn {
    display:inline-block;
    float: right;
    font: 700 16px Roboto,sans-serif;
    color: #000;
    text-decoration: none;
    margin: 20px 0;
    padding: 10px 20px;
    border-radius: 5px;
    transition: background .2s;

    &:hover {
      background-color: #fff;
    }

  }

}

