.crumb {
  display: block;
  width: 100%;
  padding: 40px 0 15px 0;

  &__item {
    font: 500 14px Roboto,sans-serif;
    color: #6d99bc;
    cursor: pointer;
    text-decoration: none;
    margin-right: 5px;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

    &:after{
      content: "/";
      padding-left: 10px;
    }

  }

}
