.star-rating {
  font-size: 0;

  &__wrap {
    display: inline-block;
  }

  &_product {
    padding: 10px 0;
    font-size: 14px;
  }

  &_seller {
    display: inline-block;
    padding: 10px 0;
    font-size: 11px;
  }

  &__wrap:after {
    content: "";
    display: table;
    clear: both;
  }
  &__ico {
    float: right;
    padding-left: 2px;
    cursor: pointer;
    color: #FFB300;

    &:last-child {
      padding-left: 0;
    }

  }

  &__input {
    display: none;
  }

  &__ico:hover:before,
  &__ico:hover ~ &__ico:before,
  &__input:checked ~ &__ico:before {
    content: "\f005";
  }

}
