.nav-header {
  position: relative;
  height: auto;
  min-height: 50px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

& .menu {
  display: inline-block;
  width: 50%;
  vertical-align: top;
  text-align: center;
  font: 700 16px Roboto,sans-serif;
  float: left;

  &__item {
    display: inline-block;
    margin: 16px 25px;
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

    &-down {
      padding-left: 10px;
    }

  }

}

.location {
  display: inline-block;
  width: 25%;
  vertical-align: top;
  font: 700 16px Roboto,sans-serif;
  float: left;

  &__item {
    display: inline-block;
    margin: 16px 0 16px 60px;
    color: #777;
    text-decoration: none;
    transition: color .2s;

    &:hover {
      color: #0D47A1;
    }

    &-down {
      padding-left: 10px;
    }

  }

}

.cart {
  display: inline-block;
  width: 25%;
  vertical-align: top;
  font: 700 16px Roboto,sans-serif;

  &__item {
    display: inline-block;
    margin: 16px 60px 16px 0;
    color: #000;
    text-decoration: none;
    text-transform: uppercase;
    transition: color .2s;
    float: right;

    &:hover {
      color: #0D47A1;
    }

    &-circ {
      position: relative;
      top: -10px;
      padding: 3px;
      border-radius: 5px;
      font-size: 12px;
      color: #fff;
      background-color: #aadd12;
      transition: background .2s;
    }

    &:hover &-circ {
      background-color: #0D47A1;
    }

  }

}


